footer{
    background: url(../img/bg3_02.png)  no-repeat center center;
    height: auto;
    .md_fluid{
        .media(max-width, none, 750px);
      }
    h2{
        padding-top: 130px;
        color: white;
        font-size: 60px;
        font-weight: 700;
        margin-bottom: 70px;
        text-align: left;
        .media(font-size, 48px, 750px);
        .media( text-align, left, 750px);
        .media( margin-bottom, 50px, 750px);
        .media( padding-top, 80px, 750px);
        .md(font-size, 48px);
        .md( text-align, left);
        .md( margin-bottom, 50px);
        .md( padding-top, 20px);
        .sm(font-size, 36px);
        .sm(margin-bottom, 30px);
        .sm(padding-top, 50px);
  
    }
    p{
        font-size: 16px;
        font-weight: 100;
        color: white;
        line-height: 30px;
        margin-bottom: 40px;
        .md(text-align, justify);
        .sm(text-align, center);
        .sm( margin-bottom, 10px);
         
    }
    .footer_forms{
        background: url(../img/2020-04-24\ 03.29.40.jpg)  no-repeat center center ;
        margin-bottom: 120px;
        padding-bottom: 70px;
        .sm(margin-bottom, 30px);
        h3{
            padding-top: 75px;
            margin-bottom: 50px;
            color: white;
            font-weight: 700;
            font-size: 23px;
            text-align: center;
        }
        input{
            display: block;
            max-width: 100%;
            background-color: transparent ;
            margin: auto;
            color: white;
            font-size: 16px;
            font-weight: 100;
            border: none;
            border-bottom: 2px solid white;
            padding-bottom: 10px;
            margin-bottom: 40px;
        }
        .btn{
            background-color: white;
            border: none;
            color: @red;
            padding: 20px 70px;
            border-radius: 50px;
            font-size: 18px;
            font-weight: 700px;
            margin: auto;
            display: block;
           

        }
    }
    .logo {
        display: flex;
       .sm(justify-content, center);
       .sm(margin-bottom, 20px);
        .box_img {
          width: auto;
        }
        a {
          color: #201717;
          text-transform: uppercase;
          margin-left: 15px;
          font-weight: 700;
          font-size: 21px;
          &:hover {
            color: @red;
            text-shadow: 2px 2px 2px black;
          }
          span {
            text-transform: none;
            font-size: 15px;
            font-weight: 100;
            display: block;
          }
        }
      }

      .social_icons{
          display: flex;
          align-items: flex-start;
          .sm(justify-content, center);
          p{
              font-size: 14px;
              margin-right: 15px;
              .sm(display, none);
              .md(display, block);
              
          }
          i{
            margin-right: 10px;
            font-size: 15px;
            color: white;
            background-color: #4c75a3;
            border-radius: 50%;
            padding: 10px;
            &:hover {
                color: @red;
                text-shadow: 2px 2px 2px black;
                box-shadow: 2px 2px 2px black;
              }
        }
      }
}