header {
  width: 100%;
  height: 950px;
  .bg("../img/headerbg_01.png");
  position: relative;
  .media(background, url(../img/750_01.png), 750px);
 .sm(background, url(../img/320_01.png) no-repeat  center center / cover);
 .sm(height, 600px );
  .header_top {
    padding-top: 40px;
    .navbar {
      display: flex;
      justify-content: space-between;
      width: 100%;
      align-items: center;
      .logo {
        display: flex;
        .box_img {
          width: auto;
        }
        a {
          color: #ffffff;
          text-transform: uppercase;
          margin-left: 15px;
          font-weight: 700;
          font-size: 21px;
          &:hover {
            color: @red;
            text-shadow: 2px 2px 2px black;
          }
          span {
            text-transform: none;
            font-size: 15px;
            font-weight: 100;
            display: block;
          }
        }
      }
      .bars {
        color: white;
        .bar {
          font-size: 18px;
          font-weight: 400;
          transition: 5ms;
          i {
            margin-left: 15px;
            font-size: 24px;
          }
          &:hover {
            color: @red;
            text-shadow: 2px 2px 2px black;
          }
        }
      }
      .menu {
        background-color: black;
        position: fixed;
        width: 20%;
        border: 2px solid @red;
        box-shadow: 2px 2px 20px black;
        padding: 20px;
        top: 0px;
        height: 100%;
        transition: 5ms;
        // display: none;
        z-index: 1111;
        right: -1000px;
        .sm-block({
          display: flex;
          right: 0;
          height: auto;
          width: 100%;
          right: 1000px;
            
        });
        &:hover {
          border: 2px solid white;
        }
        li {
          margin: 15px 0px;
          border-bottom: 2px solid gray;
          .sm(border-bottom, none);
          a {
            margin: 15px 0px;
            color: white;
            font-size: 18px;
            font-weight: 700;
            text-transform: uppercase;
            display: block;
            text-decoration: none;
            transition: 5ms;
            cursor: pointer;
            &:hover {
              color: @red;
              text-shadow: 2px 2px 2px black;
            }
            .sm-block({
              font-size: 13px;
              margin: 0px 5px;
            });
          }
        }
        i {
          color: white;
          text-align: right;
          display: block;
          font-size: 18px;
          &:hover {
            color: @red;
          }
        }
      }
    }
  }
  .header_title {
    width: 100%;
    position: absolute;
    top: 20%;
    h2 {
      font-size: 37px;
      font-weight: 100;
      color: white;
      text-align: center;
      text-transform: uppercase;
      .md(font-size, 24px );
      .media(font-size, 30px, 750px);
      .media(font-weight, 400px, 750px);
      .sm(font-size, 13px );
      .sm(font-weight, 100px );
    }
    h1 {
      text-align: center;
      text-transform: uppercase;
      font-size: 195px;
      color: white;
      font-weight: 900;
      .md(font-size, 100px );
      .media(font-size, 157px, 750px);
      .media(font-weight, 700px, 750px);
      .sm(font-size, 36px );
    }
  }
  .header_video {
    position: absolute;
    width: 100%;
    top: 55%;
    margin: auto;
    .video_title {
      font-weight: 300;
      font-size: 14px;
      color: #ffcfdf;
      width: 45%;
      position: absolute;
      right: -35%;
      top: 40%;
      display: flex;
      align-items: center;
      .media(top, 90%, 750px);
      .media(right, 0%, 750px);
        .sm(top, 40% );  
    &::before{
        content: "";
        display: inline-flex;
         height: 2px;
         background: #fff;
         padding-right: 30%;
         margin-right: 10%;
         .media(display, none, 750px);
        
    }
    }
  }
}
