section {
  .md_fluid{
    .media(max-width, none, 750px);
  }
  width: 100%;
  height: auto;
  .section_first {
    background: url(../img/Panteon-1170_02.png) no-repeat center bottom;
    margin-top: 50px;
    color: @red;
    height: 900px;
    .md(background, url(../img/Panteon-750_02.png) no-repeat center bottom);
    .sm(background, url(../img/Panteon-320_02.png) no-repeat center bottom);
    .sm(height, auto);
    .sm(padding-bottom, 160px);
    .sf_text {
      h3 {
        font-size: 48px;
        font-weight: 700;
        color: @red;
        text-align: center;
        span {
          display: block;
          margin-top: 25px;
          padding: 0px 20px;
          font-size: 13px;
          font-weight: 100;
          color: #4c4c4c;
          position: relative;

          &::after {
            content: "";
            display: block;
            width: 1px;
            height: 50px;
            background-color: #fbd6e0;
            position: absolute;
            right: 0;
            top: 0;
          }
        }
      }
    }
    h2 {
      font-weight: 700;
      font-size: 60px;
      color: @red;
      margin-top: 150px;
      margin-bottom: 50px;
      .media(font-size, 48px, 750px);
      .media(margin-top, 110px, 750px);
      .md(font-size, 48px);
      .md(margin-top, 110px);
      .sm(font-size, 36px);
      .sm(margin-top, 50px);
      .md(margin-bottom, 30px);
    }
    h3 {
      font-weight: 700;
      font-size: 24px;
      color: black;
      margin-bottom: 34px;
    }
    p {
      font-weight: 100;
      font-size: 14px;
      color: #4c4c4c;
      line-height: 30px;
      .sm(text-align, justify);
    }
   

  }
  .section_second {
    margin-top: 130px;
    .media(margin-top, 80px, 750px);
    .md(margin-top, 80px);
    .sm(margin-top, 100px);
    h2 {
      font-size: 60px;
      font-weight: 700;
      color: @red;
      margin-bottom: 50px;
      .media(font-size, 48px, 750px);
      .md(font-size, 48px);
      .sm(font-size, 36px);
      .sm(margin-bottom, 30px);
    }
    h3 {
      font-weight: 700;
      font-size: 24px;
      color: black;
      margin-bottom: 30px;

    }
    p {
      color: #4c4c4c;
      font-size: 14px;
      font-weight: 100;
      line-height: 30px;
      .media(font-size, 16px, 750px);
      .media(text-align, justify, 750px);
      .md(font-size, 16px);
      .md(text-align, justify);
      .sm(text-align, justify);
      
    }
    .red {
      color: @red;
      margin-top: 40px;
      margin-bottom: 50px;
    }
    p:type-of-last {
      margin-top: 20px;
    }
    .img_boxs {
      border: 10px solid @red;
      position: relative;
      height: 70%;
      width: 70%;
      margin-left: 20%;
      .media(height, 30%, 750px);
      .md(height, 25%);
      .sm(height, 80%);
      .sm(margin-top, 40px);
      img {
        position: relative;
        top: 50px;
        left: -60px;
        z-index: 1111;
        .media(top, 24px, 750px);
        .md(top, 20px );
      }
    }
    .section_second_bottom {
      background: url(../img/bgs_03.png) no-repeat center center / cover;
      margin-top: 70px;
      width: 100%;
      height: auto;
      .sm(margin-top, 150px);
      h3 {
        font-size: 24px;
        font-weight: 700;
        color: white;
        margin-top: 50px;
        margin-bottom: 25px;
        .sm(margin-top, 200px);
      }
      p {
        color: white;
        line-height: 30px;
        font-size: 16px;
        font-weight: 100;
      }
      .img_box {
        position: relative;
        img {
          position: absolute;
          top: -20px;
          left: 20px;
          box-shadow: 2px 2px 20px @red;
          .sm(left, 0px);
          .sm(top, -100px);
        }
      }
      .bottom {
        margin-top: 30px;
        display: flex;
        justify-content: space-around;
        margin-bottom: 50px;
        .sm(display, block);
        .sm(text-align, center);
        p {
          font-size: 60px;
          font-weight: 400;
          .media(font-size, 24px, 750px);
          .md(font-size, 24px );
          span {
            font-size: 24px;
            margin-left: 60px;
            .media(font-size, 18px, 750px);
            .md(font-size, 18px );
          }
        }
        .fly {
          background: white;
          padding: 25px 35px;
          border: none;
          border-radius: 40px;
          font-size: 18px;
          font-weight: 700;
          color: #c2529a;
          .media( padding, 25px, 750px);
          .md( padding, 25px);
          .sm(margin-top, 20px);
          &:hover {
            box-shadow: 2px 2px 20px white;
          }
        }
      }
    }
  }
  .section_three {
    background: url(../img/bg3_02.png) no-repeat center center / cover;
    width: 100%;
    position: relative;
    margin-top: 170px;
    .media( margin-top, 85px, 750px);
    .md( margin-top, 85px );
    .sm(margin-top, 0px);
    .particles-js-canvas-el {
      position: absolute;
      top: 0;
    }
    h2 {
      font-weight: 700;
      font-size: 60px;
      color: white;
      text-align: center;
      padding-top: 115px;
      margin-bottom: 70px;
      .media(font-size, 48px, 750px);
      .media( text-align, left, 750px);
      .media( margin-bottom, 50px, 750px);
      .media( padding-top, 80px, 750px);
      .md(font-size, 48px);
      .md( text-align, left);
      .md( margin-bottom, 50px);
      .md( padding-top, 80px);
      .sm(font-size, 36px);
      .sm(margin-bottom, 30px);
      .sm(padding-top, 60px);
    }
    .st_video {
      position: relative;
      top: 0;
      text-align: center;
      z-index: 1;
    }
    .st_text {
      display: flex;
      margin-top: 70px;
      padding-bottom: 150px;
      .sm( margin-top, 0px);
      .sm( display, block);
      .sm( position, relative);
      .sm( z-index, 1111);
      p {
        color: white;
        font-size: 16px;
        font-weight: 700;
      }
      .st_text_left {
        margin-right: 50px;
        width: 50%;
        .media( width, 80%, 750px);
        .md( width, 80% );
        p {
          margin-bottom: 60px;
        }
      }
      .st_text_right {
        .sm( display, none);
        p:nth-child(1) {
          margin-bottom: 45px;
          font-size: 24px;
          font-weight: 700;
        }
        p:nth-child(2) {
          margin-bottom: 25px;
          font-size: 16px;
          font-weight: 100;
        }
        p:nth-child(3) {
          margin-bottom: 25px;
          font-size: 16px;
          font-weight: 100;
        }
        p:nth-child(3) {
          font-size: 16px;
          font-weight: 100;
        }
      }
       
    }
  }
  .section_four {
    margin-top: 100px;
    .media( margin-top, 70px, 750px);
    .md( margin-top, 70px);
    .sm(margin-top, 60px);
    h2 {
      font-size: 60px;
      color: @red;
      margin-bottom: 50px;
      .media(font-size, 48px, 750px);
      .media( text-align, center, 750px);
      .media( margin-bottom, 50px, 750px);
      .media( padding-top, 0px, 750px);
      .md(font-size, 48px);
      .md( text-align, left);
      .md( margin-bottom, 50px);
      .md( padding-top, 0px);
      .sm(font-size, 36px);
      .sm(margin-bottom, 30px);
      .sm(padding-top, 0px);

    }
    .sf_text {
      margin-bottom: 150px;
      margin-top: 40px;
      .sm(margin-bottom, 20px);
      h3 {
        font-size: 24px;
        color: black;
        margin-bottom: 15px;
        font-weight: 400px;
      }
      p {
        font-size: 16px;
        font-weight: 400;
        color: #4c4c4c;
        margin-bottom: 50px;
        line-height: 30px;
      }
      .btn {
        background-color: @red;
        color: white;
        padding: 17px 42px;
        border: none;
        border-radius: 50px;
        font-size: 16px;
      }
    }
    .tabs {
      border-left: 3px solid @red;
      height: 80%;
      text-orientation:  none ;
      position: relative;
      .media(text-orientation, unset, 750px);
      .media(border-top,  3px solid @red, 750px);
      .media(border-left,  none, 750px);
      .md(text-orientation, unset);
      .md(border-top,  3px solid @red);
      .md(border-left,  none);
      ul {
        .media(display, flex, 750px);
        .media(justify-content, space-between, 750px);
        .md(display, flex);
        .md(justify-content, space-between);
        li {
          margin-bottom: 100px;
          display: flex;
          flex-direction: column; 
          .media( flex-direction, unset, 750px);
          .media( margin-bottom, 0px, 750px);
          .md( flex-direction, unset);
          .md( margin-bottom, 0px);
          &:hover::before{
              background-color: @red;
       
          }
        }
        li:nth-child(1) {
          margin-top: 0;
        }
      }
      a {
        text-decoration: none;
        margin-left: 30px;
        display: block;
        color: @red;
        writing-mode: vertical-lr;
        font-size: 20px;
        font-weight: 400;
        display: none;
        .media( margin-top, 16px, 750px);
        .media(  writing-mode, unset, 750px);
        .md( margin-top, 16px);
        .md(  writing-mode, unset);
        
        
      }
      i {
        display: block;
        margin-bottom: 20px;
        color: @red;
        margin-left: 30px;
        font-size: 20px;
        .media( margin-top, 20px, 750px);
        .media( margin-left, 0px, 750px);
        .media( margin-bottom, 40px, 750px);
        .md( margin-top, 20px);
        .md( margin-left, 0px);
        .md( margin-bottom, 40px);
      }

      li::before {
        content: "";
        display: block;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        background-color: rgba(255,255,255,1);
        border: 2px solid @red;
        position: absolute;
        left: -12px;
        .media( position, relative, 750px);
        .media( top, -9px, 750px);
        .media( left, 22px, 750px);
        .md( position, relative);
        .md( top, -9px);
        .md( left, 22px);
      }
      
     
    }
    .item_tabs{
      display: none;
    }
    .active{
      display: block;
    }
  }
}
